* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
}

.app {
  background-image: url('./assets/default-bg.jpg');
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease;
}

.app.cold {
  background-image: url('./assets/cold-bg.jpg');
}
.app.warm {
  background-image: url('./assets/warm-bg.jpg');
}
.app.very-warm {
  background-image: url('./assets/very-warm-bg.jpg');
}

main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.35));
  padding: 25px;
}

h3.title {
  text-align: center;
  color: #fff;
  font-size: 36px;
  margin: 10px 0 30px 0;
}

.inner-wrapper {
  width: 50%;
  margin: 0 auto;
}

.search-box {
  width: 100%;
  margin: 20px 0 75px;
  position: relative;
}

.search-box:before {
  content: "\f002";
  display: inline-block;
  font: normal normal normal 18px/1 FontAwesome;
  position: absolute;
  left: 15px;
  top: 16px;
  color: #666;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px 48px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  /* border-radius: 0px 0px 16px 16px; */
  border-radius: 7px;
  /* margin-top: -25px; */

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  
  color: #313131;
  font-size: 20px;

  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  margin-bottom: 5px;
}

.location-box .date {
  color: #FFF;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto 25px auto;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 16px;
  padding: 15px 25px;
  color: #FFF;
  font-size: 82px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #FFF;
  font-size: 44px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}



.api-used-section {
  border-top: 1px solid rgba(221, 221, 221, 0.20);
  text-align: center;
  margin-top: 25px;
  padding: 5px 0 5px 0;
  position: fixed;
  width: 100%;
  display: block;
  bottom: 0;
  left: 0;
}
.api-used-section p {
  font-size: 12px;
  margin: 0;
  padding: 5px 0 5px 0;
  color: #fff;
  font-weight: 500;
}
.api-used-section a {
  color: #fff;
  text-decoration: none;
  padding-left: 3px;
  margin-left: 2px;
}
.api-used-section a:hover {
  text-decoration: underline;
}
.made-with {
  color: #616a81;
  margin: 0;
  padding: 0;
  font-size: 13px;
}
.made-with .fa {
  -webkit-animation: heartbeat 1s infinite;
  animation: heartbeat 1s infinite;
  transition: all .2s ease-in-out;
}
@keyframes heartbeat
{
    0%
    {
      transform: scale( .75 );
    }
    20%
    {
      transform: scale( 1 );
      color: red;
    }
    40%
    {
      transform: scale( .75 );
    }
    60%
    {
      transform: scale( 1 );
      color: red;
    }
    80%
    {
      transform: scale( .75 );
    }
    100%
    {
      transform: scale( .75 );
    }
}



@media (max-width: 575.98px) {

  .inner-wrapper {
    width: 100%;
    margin: 0 auto;
  }

}